import React, { Component } from 'react'
import bridge from '@vkontakte/vk-bridge'
import { Group, ScreenSpinner, List, Button, Link, Header, Placeholder, ActionSheet, ActionSheetItem, Alert, SimpleCell, PullToRefresh, Checkbox, Div, Separator } from '@vkontakte/vkui'
import Icon28EditOutline from '@vkontakte/icons/dist/28/edit_outline';
import Icon28DeleteOutlineAndroid from '@vkontakte/icons/dist/28/delete_outline_android';
import Icon24Settings from '@vkontakte/icons/dist/24/settings';
import Icon28SettingsOutline from '@vkontakte/icons/dist/28/settings_outline';

import SnackbarError from './SnackbarError';

export class MyGroups extends Component {
  state = {
    data: null,
    snackbar:null,
    isFetching:false,
    deleted:[]
  }
  componentDidMount() {
    this.props.setPopout(<ScreenSpinner />)
    fetch("https://cors-anywhere.herokuapp.com/https://appvk.flights.ru/get-tags", {
      "headers": {
        "accept": "*/*",
        "accept-language": "ru-RU,ru;q=0.9,en-US;q=0.8,en;q=0.7",
        "content-type": "application/json;charset=utf-8",
      },
      "body": JSON.stringify({
        user_id: String(this.props.state.user.id)
      }),
      "method": "POST",
    })
    .then(response => response.json())
    .then(data => {
      let arrGroups = [];
      data.tags.forEach(tag => {if(arrGroups.indexOf(tag.body.split('@')[1]) === -1) arrGroups.push(tag.body.split('@')[1])});
      this.props.onChangeGroups('my_groups', arrGroups);
      let arrCategory = [];
      let arr = [];
      data.tags.forEach(tag => {if(arrCategory.indexOf(tag.category) === -1) arrCategory.push(tag.category)});
      arrCategory.forEach(category => {
        let groupsCategory = [];
        data.tags.forEach(tag => {if(groupsCategory.indexOf(tag.group_name) === -1 && tag.category === category) groupsCategory.push(tag.group_name)});
        let arrG = [];
        groupsCategory.forEach(group => {
          let u = [];
          u = data.tags.filter(tag => tag.category === category && tag.group_name == group);
          let code = data.tags.filter(tag => tag.category === category && tag.group_name == group)[0].body.split('@')[1];
          arrG.push({group, code, data:u});
        })
        arr.push({category, data: arrG})   
      })
      console.log(arr)
      this.setState({ data: arr }, this.props.setPopout(null))
    }).catch(err => {
      this.props.setPopout(null)
      this.setState({snackbar:<SnackbarError close={() => this.setState({snackbar:null})}/>, data:'error'})
    })
  }

  openMenu = (name, screenName, n, i) => {
    this.props.setPopout(
      <ActionSheet onClose={() => this.props.setPopout(null)}>
        <ActionSheetItem autoclose before={<Icon28EditOutline />} onClick={() => this.edit_(name, screenName, n, i)}>
          Редактировать
        </ActionSheetItem>
        <ActionSheetItem mode="destructive" before={<Icon28DeleteOutlineAndroid />} onClick={() => this.openConfirm(name, screenName, n, i)}>
          Удалить  
        </ActionSheetItem>
      </ActionSheet>
    )
  }

  onChangeDelete = (e) => {
    const { name , checked, id } = e.currentTarget;
    if(checked) 
      this.state.deleted.push({group:id, to:name});
    else {
      let i = this.state.deleted.findIndex(i => i.to === name);
      this.state.deleted.splice(i, 1);
    }
    console.log(this.state.deleted)
  }

  openConfirm = (name, screenName, n, i) => {
    if(this.state.deleted.findIndex(d => d.group === screenName) === -1) {
        this.props.setPopout(null)
        this.setState({snackbar:<SnackbarError close={() => this.setState({snackbar:null})} text="Вы не выбрали не одного города в данной группе." />})
    }
    else     
    this.props.setPopout(
      <Alert
      onClose={() => this.props.setPopout(null)}
      actions={[{
        title: 'Нет',
        autoclose: true,
        mode: 'cancel'
      }, {
        title: 'Да',
        autoclose: true,
        mode: 'destructive',
        action: () => this.deleteChoose(screenName, n, i),
      }]}>
       <p>Вы точно хотите удалить выбранные подписки?</p>
      </Alert>
    )
  }

  delete = (id, n, i, t) => {
    console.log(i)
    this.props.setPopout(<ScreenSpinner />);
    fetch("https://cors-anywhere.herokuapp.com/https://appvk.flights.ru/remove-tag", {
      "headers": {
        "accept": "*/*",
        "accept-language": "ru-RU,ru;q=0.9,en-US;q=0.8,en;q=0.7",
        "content-type": "application/json;charset=utf-8",
      },
      "referrer": "https://appvk.flights.ru/",
      "referrerPolicy": "no-referrer-when-downgrade",
      "body": JSON.stringify({tag_id:id, user_id:this.props.state.user.id}),
      "method": "POST",
    })
    .then(response => response.json())
    .then(check => {
      if(check.response === 'ok'){
        this.state.data[n].data[i].data.splice(t, 1);
        this.props.setPopout(null);
      }
    })
  }

   deleteChoose = (screenName, n, i) => {
      this.props.setPopout(<ScreenSpinner />)
      this.state.data[n].data[i].data.forEach(tag => {
        if(tag.body.split('@')[1] == screenName && this.state.deleted.findIndex(d => Number(d.to) === tag.id) !== -1) {
          fetch("https://cors-anywhere.herokuapp.com/https://appvk.flights.ru/remove-tag", {
            "headers": {
              "accept": "*/*",
              "accept-language": "ru-RU,ru;q=0.9,en-US;q=0.8,en;q=0.7",
              "content-type": "application/json;charset=utf-8",
            },
            "referrer": "https://appvk.flights.ru/",
            "referrerPolicy": "no-referrer-when-downgrade",
            "body": JSON.stringify({tag_id:tag.id, user_id:this.props.state.user.id}),
            "method": "POST",
          })
          .then(response => response.json())
          .then(check => {
            if(check.response === 'ok'){
              let r = this.state.data[n].data[i].data.findIndex(l => Number(l.id) === Number(tag.id));
              this.state.data[n].data[i].data.splice(r, 1);
            }
          })
        }
        this.props.setPopout(null);
      });

  }

  edit_ = (name, screenName, n, i) => {
    console.log(screenName)
    const { state } = this.props
    this.props.setPopout(<ScreenSpinner />)
    bridge.sendPromise("VKWebAppGetAuthToken", {
      app_id: state.appId,
      scope: "wall,groups",
      request_id: 1
    })
    .then(auth => {
      if(auth.access_token && auth.scope === "wall,groups"){
        bridge.sendPromise("VKWebAppCallAPIMethod", {
          method: "groups.getById",
          request_id: 2,
          params: {
            group_id: screenName,
            access_token: auth.access_token,
            v: 5.107,
          }
        })
        .then(data => {
          bridge.sendPromise("VKWebAppAllowMessagesFromGroup", {"group_id": data.response[0].id, "key": "dBuBKe1kFcdemzB"})
          .then(() => {
            bridge.sendPromise("VKWebAppCallAPIMethod", {
              method: "wall.get",
              request_id: 3,
              params: {
                domain: screenName,
                access_token: auth.access_token,
                v: 5.107,
              }
            })
            .then(async tags => {
              let tagsIn = [];
              let tagsOut = [];
              let itemsWall = tags.response.items;
              let tags_obj = this.state.data[n].data[i].data.filter(element => { if(element.body.split('@')[1] ===  screenName) { return true; }});
              let tags_name = tags_obj.map(e => { return e.body});
              console.log(tags_name);
              itemsWall.forEach((group) => {
                let match = [...group.text.matchAll(/(#.*@.*)/g)];
                if (match.length) {
                  if (match.length > 2) {
                    let matchOut = match[2];
                    tagsOut.push(matchOut)
                }
                let matchIn = match[0];
                tagsIn.push(matchIn)
                }
              });
              tagsIn = tagsIn.map((item) => 
                item[0]
              )
              tagsIn = tagsIn.filter((v,i) => tagsIn.indexOf(v) === i)
              tagsOut = tagsOut.map((item) => 
                item[0]
              )
              tagsOut = tagsOut.filter((v,i) => tagsOut.indexOf(v) === i)
              let obj = {
                name:name,
                groupId:data.response[0].id, 
                from:tagsOut.map((item) => {
                  return {
                    tag: item,
                    checked: tags_name.indexOf(item) !== -1
                  }
                }),
                to:tagsIn.map((item) => {
                  return {
                    tag: item,
                    checked: tags_name.indexOf(item) !== -1
                  }
                })
              }
              this.props.onChangeGroups('obj', obj);
              this.props.openModal('subscribeCities');
              this.props.setPopout(null);

            })
            .catch((err) => {this.props.setPopout(null); console.log(err)})
          })
          .catch((err) => {this.props.setPopout(null); console.log(err)})
        })
        .catch((err) => {this.props.setPopout(null); console.log(err)})
      }
    })
    .catch(() => this.props.setPopout(null))
  }

  onRefresh = () => {
    this.setState({ isFetching:true })
    fetch("https://cors-anywhere.herokuapp.com/https://appvk.flights.ru/get-tags", {
      "headers": {
        "accept": "*/*",
        "accept-language": "ru-RU,ru;q=0.9,en-US;q=0.8,en;q=0.7",
        "content-type": "application/json;charset=utf-8",
      },
      "body": JSON.stringify({
        user_id: String(this.props.state.user.id)
      }),
      "method": "POST",
    })
    .then(response => response.json())
    .then(data => {
      let arrGroups = [];
      data.tags.forEach(tag => {if(arrGroups.indexOf(tag.body.split('@')[1]) === -1) arrGroups.push(tag.body.split('@')[1])});
      this.props.onChangeGroups('my_groups', arrGroups);
      let arrCategory = [];
      let arr = [];
      data.tags.forEach(tag => {if(arrCategory.indexOf(tag.category) === -1) arrCategory.push(tag.category)});
      arrCategory.forEach(category => {
        let groupsCategory = [];
        data.tags.forEach(tag => {if(groupsCategory.indexOf(tag.group_name) === -1 && tag.category === category) groupsCategory.push(tag.group_name)});
        let arrG = [];
        groupsCategory.forEach(group => {
          let u = [];
          u = data.tags.filter(tag => tag.category === category && tag.group_name == group);
          let code = data.tags.filter(tag => tag.category === category && tag.group_name == group)[0].body.split('@')[1];
          arrG.push({group, code, data:u});
        })
        arr.push({category, data: arrG})   
      })
      console.log(arr)
      this.setState({ data: arr, isFetching:false });
    }).catch(err => {
      this.props.setPopout(null)
      this.setState({snackbar:<SnackbarError close={() => this.setState({snackbar:null})}/>, data:'error'})
    })
  }

  render() {
    const {
      data
    } = this.state
    return (
      <div>
      <PullToRefresh isFetching={this.state.isFetching} onRefresh={this.onRefresh}>
      <Group>
        {this.state.data === 'error' ? <Placeholder>Ошибка интернет-соеденения</Placeholder>:
        data && (
        <div>
          {data.length === 0 ? <Placeholder>У вас нет подписок.</Placeholder>:
        <List>
          {
            
              data.map((item, n) =>
                <Group key={n} header={<Header>{item.category}</Header>}>
                  {item.data.map((data, i) => 
                  <Div style={{ paddingTop:0 }}>
                    <Group header={<Header aside={<Icon24Settings onClick={() => this.openMenu(data.group, data.code, n, i)} fill="var(--accent)" height={20} width={20}/>}>{data.group}</Header>}>
                      <List>
                        {data.data.map((res, t) => 
                          <SimpleCell before={<Checkbox onChange={this.onChangeDelete} id={res.body.split('@')[1]} name={res.id}/>} after={<Link onClick={() => this.delete(res.id, n, i, t)}><Icon28DeleteOutlineAndroid /></Link>}>{res.body.split('@')[0].split('#')[1]}</SimpleCell>
                        )}
                      </List>
                    </Group>
                    <Separator />
                  </Div>  
                  )}
                </Group>
              )
            
          }
        </List>
        }
        </div>
        )
      }
      {this.state.snackbar}
      </Group>
      </PullToRefresh>
      </div>
    )
  }
}

export default MyGroups
